.joke-list {
	display: flex;
	width: 80%;
	height: 80%;
}

.jokelist-sidebar {
	background: lightpink;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 30%;
	text-align: center;
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3),
		0 15px 12px rgba(0, 0, 0, 0.3) inset 0 0 25px lightpink;
	z-index: 2;
}

.jokelist-title {
	font-size: 4rem;
	margin: 2rem;
	color: white;
	font-weight: 300;
	letter-spacing: 0.6rem;
}

.jokelist-title span {
	font-weight: 700;
	letter-spacing: 0;
}

.jokelist-sidebar img {
	width: 50%;
	border-radius: 50%;
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3);
}

.jokelist-jokes {
	height: 90%;
	background: white;
	align-self: center;
	width: 70%;
	overflow: scroll;
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3);
}

.spinner {
	text-align: center;
}

.jokelist-getmore {
	font-size: 1.5rem;
	width: 50%;
	border-radius: 2rem;
	padding: 1rem 2rem;
	color: white;
	font-weight: 700;
	border: none;
	outline: none;
	margin: 2rem;
	transition: 0.8s ease;
	cursor: pointer;
	background: linear-gradient(
		-135deg,
		rgba(179, 229, 252, 1) 0%,
		rgba(179, 229, 252, 1) 50%,
		rgba(240, 98, 146, 1) 50%,
		rgba(240, 98, 146, 1) 100%
	);
}

.jokelist-getmore:hover {
	box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3);
}
